@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{
  box-sizing: border-box;
}

body{
  font-family: 'Comfortaa', cursive;
}

main{
  height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0,0,0,0.2), rgba(0,0,0,0.55));
  padding: 15px;
}
.bgVideo{
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: -100;
  object-fit: cover;
}

.allData.cloudy{

}

.allData.rainy{

}

.allData.snowy{

}

.search_area{
  width: 100%;
  margin: 0 0 25px;
}

.search_bar{
  display: block;
  width: 100%;
  padding: 10px ;
  -webkit-appearance: none;
          appearance: none;
  background: none;
  border: none;
  outline: none;
  background-color: rgba(255,255,255,0.5);
  border-radius: 0 0 15px 15px;
  margin-top: -15px;
  box-shadow: 0 4px rgba(255,255,255,0.2);
  transition: 0.4s ease;
}

.search_bar::-webkit-input-placeholder{
  font-size: 15px;
  color: #313131;
}

.search_bar:-ms-input-placeholder{
  font-size: 15px;
  color: #313131;
}

.search_bar::placeholder{
  font-size: 15px;
  color: #313131;
}

.search_bar:focus{
  background-color: rgba(255,255,255,0.75);
}

#weatherSection{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-direction: column;
          flex-direction: column;
}


.cityName{
  text-align: center;
  color: white;
  font-size: 30px;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.status{
  text-align: center;
  color: white;
  font-size: 20px;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
  margin: 10px auto 0px auto;
}

.temparea{
  text-align: center;
  background-color: rgba(255,255,255,0.35);
  border-radius: 15px;
  margin: 10px auto;
  width: 40%;
}

.temparea span{
  line-height: 100%;
}

.flipArea{
  transition: -webkit-transform 2s;
  transition: transform 2s;
  transition: transform 2s, -webkit-transform 2s;
  cursor: pointer;
}
.temp{
  color: white;
  display: inline-block;
  text-align: center;
  font-size: 50px;
  font-weight: bold;
  padding: 10px 15px;
}

.frontSide, .backSide{
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 10px auto;
  left: 0;
  right: 0;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.backSide{
  transition: 0.4s ease;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 15px 0;
  text-align: center;
}

.backSide img{
  width: 25px;
}

.flipped{
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  transition: 0.4s ease;
}

#weeklyBar{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  position: relative;
  top: 0px;
  border-top: 2px solid white;
  border-bottom: 2px solid white;
  width: 100%;
  color: white;
  padding-bottom: 10px;
}

#weeklyBar img{ 
  width: 100%;
}

.weeklyCondition:nth-child(8){
  display: none;
}
